* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.search-form {
  background-color: #3f56b3;
  justify-content: center;
  margin-bottom: 20px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

input[type="text"] {
  width: 250px;
  cursor: pointer;
  letter-spacing: .04em;
  color: #000;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 8px 0 0 8px;
  outline: none;
  padding-left: 10px;
  font-size: 14px;
  line-height: 1.33;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

button[type="submit"] {
  cursor: pointer;
  border: none;
  border-radius: 0 8px 8px 0;
}

button[type="submit"]:hover, button[type="submit"]:focus {
  background-color: #dcdcdc;
}

.btn-icon {
  fill: #646464;
  padding-top: 5px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

button[type="submit"]:hover .btn-icon, button[type="submit"]:focus .btn-icon {
  fill: #282828;
}

.footer-container {
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

button[type="button"] {
  text-align: center;
  letter-spacing: .04em;
  color: #fff;
  cursor: pointer;
  background-color: #3f56b3;
  border: none;
  border-radius: 8px;
  padding: 10px 18px;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.5;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

button[type="button"]:hover, button[type="button"]:focus {
  background-color: #324490;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

